import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-cards";

import "./Swiper.scss";

// import Swiper core and required modules
import SwiperCore, { EffectCards } from "swiper";

// install Swiper modules
SwiperCore.use([EffectCards]);

export default function App() {
  return (
    <>
      <Swiper effect={"cards"} grabCursor={true} className="mySwiper">
        <SwiperSlide>
          <pr>
            -Эркаклар бўлмаса айтилмас азон, -Эркаклар бўлмаса қайнамас қозон,
            -Эркаклар бўлмаса чақалоқ қайда, -Эркаклар бўлмаса шапалоқ қайда,
            -Эркаклар бўлмаса мўйлов қайдадир, -Эркаклар бўлмаса куёв қайдадир,
            -Дунё тургунча турсин эркаклар, -Доимо хурматда юрсин эркаклар!
            Байрам муборак
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
            Saflarda qad kerib turgan ey askar, Bilgil, mardni ko‘rsa, chekinar
            xatar. Yurakni, bilakni etgil safarbar, Gulday diyoringni asra
            hamisha! Duosi, so‘zlari yodingda tursin, Nur ingan yuzlari yodingda
            tursin, Mehrli ko‘zlari yodingda tursin, Onaizoringni asra hamisha!
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
            Қай маконда қай томонда, Тиними йўқ шом азонда, Не бўлса хам иссиқ
            жонда, Йигит боши омон бўлсин. Доим дейди уйим жойим, Халол мехнат
            қилсин доим, Ризқин улуғ қил худойим, Йигит боши омон бўлсин.
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
            Юртим, сени қадим достонларинг бор, Жаннатга баробар бўстонларинг,
            Шаҳид кетган Фитрат, Чўлпонларинг бор, Сарҳадингда мард ўғлонларинг
            бор, Қўрғони мустаҳкам кошона-Ватан.
          </pr>
        </SwiperSlide>
        <SwiperSlide>
          <pr>
            С 14 февраля! Пусть счастье Большого чувства согревает вас
            Влюбленный над собой уже не властен. Готов лететь на свет любимых
            глаз. Пусть только радость вам влюбленность дарит, Пусть
            вдохновляет, за собой ведет, И время чувства ваши пусть не старит,
          </pr>
        </SwiperSlide>
      </Swiper>
    </>
  );
}
